'use client'

import Navbar from '@/components/Navbar';
import Link from 'next/link';
import Image from 'next/image';
import { useRouter } from 'next/navigation';
import { FormEvent, useState } from 'react';
import { doCredentialLogin } from '@/authActions';

export default function LoginPage() {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);
    setError('');

    const formData = new FormData(e.currentTarget);
    const email = formData.get('email') as string;
    const password = formData.get('password') as string;

    try {
      const response = await doCredentialLogin(email, password);
      
      if (response?.error) {
        setError(response.error);
      } else {
        // Login successful, redirect to authenticated view
        router.push('/authenticated-view');
      }
    } catch (err) {
      setError('Login failed. Please try again.');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-linear-to-br from-[#4C1B7A] via-[#3B0270] to-[#2D0157] text-white">
      <Navbar />

      {/* Main content */}
      <div className="flex items-stretch min-h-[calc(100vh-80px)]">
        {/* Left side - Image */}
        <div className="hidden lg:flex flex-1 items-center justify-center overflow-hidden">
          <div className="relative w-full h-full">
            <Image
              src="/imageforcreateaccount 1.png"
              alt="Food"
              fill
              className="object-cover"
            />
          </div>
        </div>

        {/* Right side - Form */}
        <div className="flex-1 flex items-center justify-center">
          <div className="relative w-auto h-full">
            {/* Background rectangle */}
            <Image
              src="/Rectangle 24.png"
              alt="Background"
              fill
              className="object-cover"
            />
            
            {/* Form content */}
            <div className="relative z-10 h-full flex flex-col items-center justify-center p-8">
              <div className="w-full max-w-sm">
                <h1 className="text-4xl font-bold mb-8 text-center text-black">
                  <span style={{ fontFamily: "JejuHallasan" }}>Login</span>
                </h1>

                <form className="space-y-6" onSubmit={handleSubmit}>
                  {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
                      {error}
                    </div>
                  )}

                  <div>
                    <label className="block text-sm font-semibold mb-2 text-black">Email</label>
                    <input
                      type="email"
                      name="email"
                      className="w-full px-4 py-2 bg-white border border-gray-400 rounded text-black placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-[#4C1B7A]"
                      placeholder="Email"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-semibold mb-2 text-black">Password</label>
                    <input
                      type="password"
                      name="password"
                      className="w-full px-4 py-2 bg-white border border-gray-400 rounded text-black placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-[#4C1B7A]"
                      placeholder="password"
                      required
                    />
                  </div>

                  <button
                    type="submit"
                    disabled={isLoading}
                    className="w-full px-6 py-3 bg-[#4C1B7A] text-white font-bold rounded hover:bg-[#3B0270] transition mt-8 disabled:opacity-50"
                  >
                    {isLoading ? 'Logging in...' : 'Submit'}
                  </button>
                </form>

                <p className="text-center text-black mt-6 text-sm">
                  Don't have an account?{' '}
                  <Link 
                    href="/create-account-page"
                    className="text-[#4C1B7A] font-bold hover:underline"
                  >
                    Create one
                  </Link>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Decorative bubbles */}
      <div className="fixed bottom-0 left-0 -translate-x-3/8 translate-y-3/8 h-90 w-90 rounded-full bg-purple-900 blur-3xl opacity-40 z-[-1]"></div>
      <div className="fixed top-0 right-0 translate-x-1/4 h-80 w-80 rounded-full bg-purple-900 blur-3xl opacity-40 z-[-1]"></div>
    </div>
  );
}
