'use client'

import Link from 'next/link';
import Image from 'next/image';
import { usePathname } from 'next/navigation';

export default function Navbar() {
  const pathname = usePathname();

  const isLoginPage = pathname === '/login-page';
  const isCreateAccountPage = pathname === '/create-account-page';

  return (
    <div className="flex justify-between items-center p-6 border-b border-purple-600">
      <div className="flex items-center gap-2">
        <Image src="/teacup_dog_paw_logo.svg" alt="DawgSnacks" width={40} height={40} />
        <span style={{ fontFamily: "JejuHallasan", fontSize: "24px", fontWeight: "bold" }}>Dawg Snacks</span>
      </div>
      <div className="flex gap-4">
        <Link href="/create-account-page" className="px-6 py-2 hover:opacity-80 transition">
          Create Account
        </Link>
        <Link 
          href="/login-page" 
          className={`px-6 py-2 rounded-full font-bold transition ${
            isLoginPage 
              ? 'bg-white text-[#4C1B7A] hover:bg-yellow-300' 
              : 'text-white hover:bg-white hover:text-[#4C1B7A]'
          }`}
        >
          Login
        </Link>
      </div>
    </div>
  );
}
